import React from 'react';
import {
  Sidebar,
  SidebarHeader,
  SidebarContent,
  SidebarMenuContent,
  SidebarToggleButton,
  PersonaMenuItem,
} from '../styles/GlobalStyle';

interface PersonaSidebarProps {
  selectedItem: string | null;
  onSelectItem: (itemId: string | null) => void;
  isCollapsed?: boolean;
  onToggle?: () => void;
}

const PersonaSidebar: React.FC<PersonaSidebarProps> = ({
  selectedItem,
  onSelectItem,
  isCollapsed = false,
  onToggle,
}) => {
  const menuItems = [
    { id: 'training', name: '' },
    { id: 'verbalization', name: '' },
    { id: 'stimulus', name: '' },
    { id: 'vault', name: '' },
    { id: 'recent', name: ' ' },
    { id: 'admin', name: '' },
  ];

  // ""     .
  const shouldShowToggleButton = selectedItem !== 'vault' && onToggle;

  return (
    <Sidebar width="200px" $isCollapsed={isCollapsed} $variant="persona">
      {shouldShowToggleButton && (
        <SidebarToggleButton onClick={onToggle}>
        </SidebarToggleButton>
      )}
      
      {/*   ( ) */}
      <SidebarHeader $isCollapsed={isCollapsed}>
        {/*   -     */}
      </SidebarHeader>

      <SidebarContent $isCollapsed={isCollapsed}>
        <SidebarMenuContent $variant="persona">
          {menuItems.map((item) => (
            <PersonaMenuItem
              key={item.id}
              $isSelected={selectedItem === item.id}
              onClick={() => onSelectItem(item.id)}
            >
              {item.name}
            </PersonaMenuItem>
          ))}
        </SidebarMenuContent>
      </SidebarContent>
    </Sidebar>
  );
};

export default PersonaSidebar; 