import { useState, useEffect, useCallback } from 'react';
import { sentenceApi } from '../../training/api/sentenceApi';

export const useSentenceData = (threadId?: string) => {
  const [memos, setMemos] = useState<Record<string, string>>({});
  const [highlightedSentences, setHighlightedSentences] = useState<Set<string>>(new Set());
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const loadThreadSentenceData = useCallback(async () => {
    if (!threadId) return;
    
    console.log('===      ===');
    console.log('  ID:', threadId);
    
    setIsLoading(true);
    setError(null);
    
    try {
      const data = await sentenceApi.getThreadSentenceData(threadId);
      
      console.log(' :', {
        memo_count: Object.keys(data.memos || {}).length,
        highlight_count: data.highlights?.length || 0,
        memos: data.memos,
        highlights: data.highlights
      });
      
      setMemos(data.memos || {});
      setHighlightedSentences(new Set(data.highlights || []));
      
      console.log('    ');
    } catch (error) {
      console.error('    :', error);
      setError(error instanceof Error ? error.message : '  ');
      
      //     
      setMemos({});
      setHighlightedSentences(new Set());
    } finally {
      setIsLoading(false);
    }
  }, [threadId]);

  //  /  
  useEffect(() => {
    if (threadId) {
      loadThreadSentenceData();
    }
  }, [threadId, loadThreadSentenceData]);

  const handleMemoChange = async (
    sentenceId: string, 
    memo: string, 
    threadId?: string, 
    threadType?: string,
    sentenceContent?: string
  ) => {
    console.log('===     ===');
    console.log(' ID:', sentenceId);
    console.log(' :', memo.substring(0, 50) + (memo.length > 50 ? '...' : ''));
    console.log(' ID:', threadId);
    console.log(' :', threadType);
    
    try {
      //      (UI )
      setMemos(prev => ({
        ...prev,
        [sentenceId]: memo
      }));
      
      //  API 
      const response = await sentenceApi.createOrUpdateMemo({
        sentence_id: sentenceId,
        thread_id: threadId,
        thread_type: threadType,
        content: memo,
        sentence_content: sentenceContent
      });
      
      console.log('  API :', response);
      console.log('   ');
      
      return response;
    } catch (error) {
      console.error('  :', error);
      
      //     
      setMemos(prev => {
        const newMemos = { ...prev };
        if (memo.trim() === '') {
          delete newMemos[sentenceId];
        } else {
          //      
          loadThreadSentenceData();
        }
        return newMemos;
      });
      
      throw error;
    }
  };

  const handleDeleteMemo = async (sentenceId: string) => {
    console.log('===     ===');
    console.log(' ID:', sentenceId);
    
    try {
      //      (UI )
      setMemos(prev => {
        const newMemos = { ...prev };
        delete newMemos[sentenceId];
        return newMemos;
      });
      
      //  API 
      const response = await sentenceApi.deleteMemo(sentenceId);
      
      console.log('  API :', response);
      console.log('   ');
      
      return response;
    } catch (error) {
      console.error('  :', error);
      
      //     
      if (memos[sentenceId] !== undefined) {
        setMemos(prev => ({
          ...prev,
          [sentenceId]: memos[sentenceId]
        }));
      }
      
      throw error;
    }
  };

  const handleToggleHighlight = async (
    sentenceId: string, 
    threadId?: string, 
    threadType?: string
  ) => {
    console.log('===     ===');
    console.log(' ID:', sentenceId);
    console.log('  :', highlightedSentences.has(sentenceId));
    
    const isCurrentlyHighlighted = highlightedSentences.has(sentenceId);
    
    try {
      //      (UI )
      setHighlightedSentences(prev => {
        const newSet = new Set(prev);
        if (isCurrentlyHighlighted) {
          newSet.delete(sentenceId);
        } else {
          newSet.add(sentenceId);
        }
        return newSet;
      });
      
      //  API 
      let response;
      if (isCurrentlyHighlighted) {
        response = await sentenceApi.deleteHighlight(sentenceId);
      } else {
        // threadId threadType    
        response = await sentenceApi.createHighlight({
          sentence_id: sentenceId,
          thread_id: threadId || '',
          thread_type: threadType || ''
        });
      }
      
      console.log('  API :', response);
      console.log('   ');
      
      return response;
    } catch (error) {
      console.error('  :', error);
      
      //     
      setHighlightedSentences(prev => {
        const newSet = new Set(prev);
        if (isCurrentlyHighlighted) {
          newSet.add(sentenceId);
        } else {
          newSet.delete(sentenceId);
        }
        return newSet;
      });
      
      throw error;
    }
  };

  return {
    memos,
    highlightedSentences,
    isLoading,
    error,
    loadThreadSentenceData,
    handleMemoChange,
    handleDeleteMemo,
    handleToggleHighlight,
    setMemos,
    setHighlightedSentences
  };
}; 